
.text--large {
  font-size: 2.4rem;
  line-height: 3.8rem;
  font-weight: $font-primary;
}


@mixin text-styles() {

  h2,
  h3,
  h4 {
    font-weight: $base-heading-font;
    margin-top: $gutter;
  }

  .media--image {
    img {
      max-width: 100%;
      width: auto;
    }

    + h2,
    + h3,
    + h4,
    p {
      margin-top: 3rem;
    }
  }
}

.entry-content {
  @include text-styles;
}

figure {
  padding: 0;
  margin: 0;

  img {
    display: block;
  }
  figcaption {
    border-bottom: .1rem solid $border-color;
    font-size: $font-small;
    padding: .5rem;
  }

  &.align-left {
    margin-right: $gutter;
  }

  &.align-right {
    margin-right: $gutter;
  }
}


